.notificacion-flotante {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 9999;
    opacity: 0.9;
    transition: opacity 0.5s ease;
  }
  
  .notificacion-flotante.top {
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .notificacion-flotante.bottom {
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
  